import React, { Component } from 'react';
import './Message.css';

const acceptedMessages = [
  '–û–∫–µ—é—à–∫–∏!',
  '–ö—Ä–∞—Å–∏–≤–∞—è —É –Ω–∏—Ö –ø–ª–æ—â–∞–¥—å –≤ —Ü–µ–Ω—Ç—Ä–µ!',
  '–û—á–µ–Ω—å —Ö–æ—Ä–æ—à–æ!',
  '–ö–æ–º–ø—å—é—Ç–µ—Ä–∞ –Ω–µ –æ–±—ã–≥—Ä–∞–µ—à—å!',
  '–ö–æ—Ñ–µ —É –Ω–∏—Ö —Ç–∞–∫ —Å–µ–±–µ.',
  '–í–æ—Ç –±—ã —Ç—É–¥–∞ —Å–≥–æ–Ω—è—Ç—å –Ω–∞ –≤—ã—Ö–æ–¥–Ω—ã–µ!'
]

class Message extends Component {
  constructor(props) {
    super(props);
    this.state = { timer: null, isShown: false };
    this.msgRef = React.createRef();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.message.id !== this.props.message.id) {
      this.setState({ isShown: true });
      this.msgRef.current.classList.remove('visually-hidden');
      this.runTimer(this.props.message.id);
    }
  }
  
  runTimer(id) {
    this.timer = setTimeout(function() {
      this.setState({ isShown: false });
      this.msgRef.current.classList.add('visually-hidden');
      if (id !== this.props.message.id) {
        clearInterval(this.timer);
        this.setState({ isShown: true });
        this.msgRef.current.classList.remove('visually-hidden');
      }
    }.bind(this), 5000);
  }
  
  renderMsg() {
    if (this.props.gameEnded) return;
    let msg, messageElement;
    
    if (this.props.isAccepted) {
      let displayChance = Math.random() * 100;
      
      if (displayChance > 66) {
        msg = acceptedMessages[Math.floor(Math.random() * acceptedMessages.length)];
        return <p className="guess__message">{msg}</p>;
      }
    }
    
    if (this.props.message.code) {
      switch(this.props.message.code) {
        case 1:
          msg = <React.Fragment>
            –ì–æ—Ä–æ–¥ <span className="guess__accent">{this.props.message.value}</span> —É–∂–µ –±—ã–ª —Å—ã–≥—Ä–∞–Ω –≤ —ç—Ç–æ–º –º–∞—Ç—á–µ.
          </React.Fragment>
          break;
        case 2:
          msg = <React.Fragment>
            –ù–µ –∑–Ω–∞—é –≥–æ—Ä–æ–¥–∞ <span className="guess__accent">{this.props.message.value}</span>
          </React.Fragment>
          break;
        case 3:
          let lastLetters = [];
          if (this.props.message.value === '–ò' || this.props.message.value === '–ô') {
            lastLetters = ['–ò', '–ô'];
          }
          if (this.props.message.value === '–®' || this.props.message.value === '–©') {
            lastLetters = ['–®', '–©'];
          }
          
          if (!lastLetters.length) {
            msg = <React.Fragment>
              –ù—É–∂–Ω–æ —Å—ã–≥—Ä–∞—Ç—å –≥–æ—Ä–æ–¥ –Ω–∞ <span className="guess__accent">{this.props.message.value}</span>
            </React.Fragment>
          } else {
            
            msg = <React.Fragment>
              –ù—É–∂–Ω–æ —Å—ã–≥—Ä–∞—Ç—å –≥–æ—Ä–æ–¥ –Ω–∞ <span className="guess__accent">{lastLetters[0]}</span> –∏–ª–∏ <span className="guess__accent">{lastLetters[1]}</span>
            </React.Fragment>
          }
          break;
        default:
          msg = "";
      }
      
      messageElement = <p className="guess__message">{msg}</p>;
      return messageElement;
    }
  }
  
  render() {
    return (
      <div className="guess__msg-wrapper" ref={this.msgRef}>
        {this.renderMsg()}
      </div>
    );
  }
}

export default Message;